// all pages
import './pages/landing_page.tag';
import './pages/search_page.tag';
import './pages/reload_page.tag';
import './pages/getting_started_page.tag';

// stores
import config from './config';

class Pages {
    constructor() {
        this._pages = [
            { 
                view : 'reload_page',
                authenticated: true,
                notNavItem: true
            },
            {
                view : 'landing_page',
                authenticated: true,
                subscribed: true,
                default: true
            },
            {
                view : 'search_page',
                authenticated: true,
                subscribed: true,
                notNavItem: true
            },
            {
                view: 'getting_started_page',
                authenticated: false,
                subscribed: false
            }
        ]
    }

    getLandingView() {
        return "landing_page";
    }

    getViews() {
        return _.map(this._pages, 'view');
    }

    getNavItems() {
        let navItems = _.filter(this._pages, (page) => this.isViewAllowed(page.view) && !page.notNavItem);
        return navItems;
    }

    getDefaultView() {
        let page = _.find(this._pages, (page) => this.isViewAllowed(page.view) && page.default);
        if(page) return page.view;
        else return "landing_page";
    }

    isViewAllowed(view) {
        let page = this._getPageFromView(view);
        return true;
    }

    _getPageFromView(view) {
        return _.find(this._pages, (page) => page.view == view);
    }

}

export default new Pages();
