import _ from 'lodash';
import cookies from 'cookies-js';
import config from '../config';

const uuid = ()=>([1e7]+-1e3+-4e3+-8e3+-1e11).replace(/[018]/g,a=>(a^Math.random()*16>>a/4).toString(16));

const ONE_YEAR = 365 * 24 * 60 * 60;
const TEN_MINUTES = 60 * 10;

class UserStore {

    constructor() {
        this._languages = [
            {code: "en", name: "English"},
            {code: "nl", name: "Dutch"}
        ];

        this.age = 12;
        this.region = "NL";
        this.languageCode = "nl";
        this.bindEvents();
    }

    bindEvents() {
        riot.control.on(riot.EVT.changeAge, newAge => {
            this.age = newAge;
            console.info("Changing age to " + newAge);
            if(config.saveUserStateInCookie) {
               cookies.set("wfc_age", newAge, {expires: ONE_YEAR});
            }
        });
    }

    getRegion() {
        return this.region;
    }
    
    getLanguages() {
        return this._languages;
    }

    getLanguageCode() {
        return this.languageCode;
    }
    
    getAge() {
        return this.age;
    }

    setAge(age) {
        this.age = age;
    }

    getUUID() {
        if(this.uuid) {
            return this.uuid;
        } else {
            if(cookies.get("wfc_sdk_uuid")) {
                this.uuid = cookies.get("wfc_sdk_uuid");
                return this.uuid;
            } else {
                this.uuid = uuid();
                cookies.set("wfc_sdk_uuid", this.uuid, {expires: ONE_YEAR});
                return this.uuid;
            }
        }
    }

    getSessionUUID() {
        if(!this.sessionUUID) this.sessionUUID = uuid();
        cookies.set("wfc_sdk_session_uuid", this.sessionUUID, TEN_MINUTES)
        return this.sessionUUID;
    }
}

let userStore = new UserStore();

export default userStore;
