import config from '../config';
import _ from 'lodash';
import axios from 'axios';

class Api {
    constructor() {
    }

    call(endpoint, method, data, params, headers) {
        let opts = {
            url: endpoint,
            method: method,
            params: _.defaults({}, params || {}, { bust: (Date.now()) }),
        };
        if(data) {
            opts.data = data;
        }
		if(config.getCustomHeaders()) {
			opts.headers = config.getCustomHeaders();
		}
		if(headers) {
			opts.headers = _.defaults({}, opts.headers, headers);
		}
        return axios(opts);
    }

    doGet(endpoint, data, params, headers) {
        return this.call(endpoint, 'get', data, params, headers);
    }

    doPost(endpoint, data, params, headers) {
        return this.call(endpoint, 'post', data, params, headers);
    }

    doPut(endpoint, data, params) {
        return this.call(endpoint, 'put', data, params);
    }
}

export default new Api();

