class Config {
  constructor() {
    // set by integrator
    this.searchApiEndpoint = null;
    this.autocompleteApiEndpoint = null;
    this.customSearchEngines = null;
    this.defaultCustomSearchEngineId = null;
    this.customLogo = null;
    this.customFooterLogo = null;
    this.externalLinks = null;
    this.scopes = null;
    this.customHeaders = null;
    this.customQuickGuide = null;
    this.showDefaultFooterLinks = null;
    this.customFooterLinks = null;
    this.showDefaultLegalLinks = null;
    this.customLegalLinks = null;
    this.customImageBanner = null;
    this.customHtmlContent = null;
    this.customCssFile = null;
    this.showAgeSelector = null;
    this.customAgeRange = null;
    this.showLevelSelector = null;
    this.showCseSelector = null;
    this.showHeader = null;
    this.showFooter = null;
    this.showDefaultQuickGuide = null;
    this.showHelpMenu = null;
    this.resultsListWidth = null;

    this.privacyUrl =
      __ENV__.PRIVACY_URL || 'https://www.wizenoze.com/privacy-statement/';
    this.defaultLocale = __ENV__.DEFAULT_LOCALE || 'nl';
    //this.scopes = __ENV__.SCOPES || ["pages", "videos", "images"];
    this.logo = __ENV__.LOGO || 'logo-wfc-small-new.png';
    //this.logoLanding = __ENV__.LOGO_LANDING || "logo-wfc-landing.png";
    this.supportMail = __ENV__.SUPPORT_MAIL || 'support@wizenoze.com';
    this._showFooter = __ENV__.SHOW_FOOTER;
    this.helpLink = __ENV__.HELP_LINK;
    this.helpLinkPrefix =
      __ENV__.HELP_LINK_PREFIX ||
      'https://webforclassrooms.com/frequently-asked-questions/';
    //this.copyrightLink = __ENV__.COPYRIGHT_LINK || "http://over.jouwzoekmachine.nl/copyright/";
    this.application = __ENV__.APPLICATION || 'W4C';
    this.regions = __ENV__.REGIONS;
    this.locales = __ENV__.LOCALES;
    this.faqLink = __ENV__.FAQ_LINK;
    this.termsLink = __ENV__.TERMS_URL;
    this.publicPath = __ENV__.PUBLIC_PATH;
  }

  getCustomHeaders() {
    return this.customHeaders;
  }

  getPublicPath() {
    return this.publicPath;
  }

  getRegions() {
    return this.regions;
  }

  getLocales() {
    return this.locales;
  }

  isSingleRegion() {
    return Object.values(this.getRegions()).length == 1;
  }

  getApplication() {
    return this.application;
  }

  getDefaultLocale() {
    return this.defaultLocale;
  }

  getHelpLink() {
    return this.helpLink;
  }

  getHelpLinkPrefix() {
    return this.helpLinkPrefix;
  }

  showFooter() {
    return this._showFooter;
  }

  getScopes() {
    return this.scopes;
  }

  isDevMode() {
    return (
      window.location.host.startsWith('localhost') ||
      window.location.host.startsWith('192.') ||
      window.location.host.startsWith('10.')
    );
  }

  getPrivacyUrl() {
    return this.privacyUrl;
  }

  getTermsUrl() {
    return this.termsLink;
  }

  getFaqLink() {
    return this.faqLink;
  }

  getLogo() {
    return this.logo;
  }

  getCustomLogo() {
    return this.customLogo;
  }

  getSupportMail() {
    return this.supportMail;
  }

  loadCustomHtmlContent() {
    if (this.customHtmlContent == null) return;
    fetch(this.customHtmlContent)
      .then(function(response) {
        return response.text();
      })
      .then(function(text) {
        document.getElementById('customHtmlContent').innerHTML = text;
      })
      .catch(function(error) {
        console.log('Looks like there was a problem: \n', error);
      });
  }

  loadCustomCssFile() {
    let head = document.getElementsByTagName('HEAD')[0];

    // Create new link Element
    let link = document.createElement('link');

    // set the attributes for link element
    link.rel = 'stylesheet';
    link.type = 'text/css';
    link.href = this.customCssFile;

    // Append link element to HTML head
    head.appendChild(link);
  }
}

export default new Config();
