import route from 'riot-route';
import _ from 'lodash';

import app from './app';
import config from './config';
import audiencesStore from './stores/AudiencesStore';
import searchParamStore from './stores/SearchParamStore';
import pages from './pages';


// we need this to easily check the current route from every component
riot.routeState = {
    view: ''
};

riot.route = route;

class Router {

    constructor() {
        this.MAX_ROUTE_HISTORY = 50;

        //riot.route.base("/");  

        this.path = null;
        this.routeName = null;
        this.params = {};
        this.query = {};

        this.routeHistory = [];
    }

    getRouteHistory() {
        return this.routeHistory;
    }

    getRouteName() {
        return this.routeName;
    }

    getParams() {
        return this.params;
    }

    getQuery() {
        return this.query;
    }

    getPath() {
        return this.path;
    }

    route(path) {
        this.path = path;
        riot.route(path);
        this.query = riot.route.query();
        this.pushRouteHistory();
    }

    pushRouteHistory() {
        if(this.routeHistory.length > this.MAX_ROUTE_HISTORY) this.routeHistory.shift();
        this.routeHistory.push({route: this.routeName, params: this.params, query: this.query});
    }

    init(anchorId) {
        this.anchorId = anchorId;
        this.start(true);
    }

    start(autoexec) {
        riot.route("/age-*/search..", age => {
            this.routeSearchPage(age);
        });

        this._currentView = null;
        this._views = pages.getViews();

        riot.route(this._handleRoute.bind(this));

        riot.route.start(autoexec);
    }

    _handleRoute(view, extra) {
        if(view == "search_page") return;
        if(riot.route.query().language) {
            riot.control.trigger(riot.EVT.changeLanguage, riot.route.query().language);      
        }
        // load default view, if view is not in views list or is not allowed
        if (this._views.indexOf(view) === -1 || !pages.isViewAllowed(view)) {
            return riot.route(pages.getDefaultView());
        }        
        this._loadView(view, extra);
    }

    routePageWithoutHistory(routeName, view) {
        this.routeName = routeName;
        this.params = {};
        this.query = {};
        this.pushRouteHistory();
        return this._loadView(view);
    }

    routeSearchPage(age) {
        if(age > audiencesStore.MAX_AGE || age < audiencesStore.MIN_AGE) {
                console.error("Invalid age, redirecting to landing page");
                riot.route("/", null, true);
                return;
        }

        this.routeName = "search";
        this.params = {age: age};
        this.query = riot.route.query();
        this.pushRouteHistory();

        riot.control.trigger(riot.EVT.changeAge, age);


        let cseId = parseInt(decodeURIComponent(riot.route.query()["cse"]), 10);
        let cse = _.find(config.customSearchEngines, {id: cseId});
        // validate cseId is in config
        if(cse) {
            riot.control.trigger(riot.EVT.changeCse, cseId);
        }

        let setParamFromQuery = (event, qname) => {
            if(riot.route.query()[qname]) {
                try {
                    riot.control.trigger(event, decodeURIComponent(riot.route.query()[qname]));
                } catch(ex) {
                    console.error("Could not trigger event with uri component", ex);
                }
            }
        };
        setParamFromQuery(riot.EVT.changeQuery, "q");
        //setParamFromQuery(riot.EVT.changeLevel, "level");
        
        let levels = [];
        let level = decodeURIComponent(riot.route.query()["level"]);
        if(level != "undefined"){
            levels = level.split(",");
            levels = levels.map((element) => parseInt(element));
            levels = levels.filter(Boolean);
            riot.control.trigger(riot.EVT.changeLevels, levels);
        }
        
        // reset page to 1 if we have an images scope, because it's too hard to maintain scroll position with infinite scrolling
        if(decodeURIComponent(riot.route.query()["scope"]) == 'images') {
            riot.control.trigger(riot.EVT.changePage, 1);
        } else {
            setParamFromQuery(riot.EVT.changePage, "page");
        }
        setParamFromQuery(riot.EVT.changeScope, "scope");      

        if(this._currentView != "search_page") {
            return this._loadView("search_page");
        } else {
            return null;
        }
    }

    stop() {
        riot.route.stop();
    }

    _loadView(view, extra) {
        if (this._currentView) {
            this._currentView.unmount(true);
        }

        riot.routeState.view = view;
        this._currentView = riot.mount('#' + this.anchorId, view, {extra: extra})[0];
        return this._currentView;
    }

    setLocationBarWithoutRouting(path) {
        if(path.length < 2) return;
        //XXX: this might be brittle if we drop the hash handling
        let location = document.location.pathname + document.location.hash;
        if(location.indexOf(path.substring(1)) != -1) {
            return;
        }
        this.stop();
        this.route(path);
        this.start(false);
    }
}

riot.wnrouter = new Router();

export default riot.wnrouter;