import _ from 'lodash';
import config from '../config';

class StyleStore {
    constructor() {
    }
    
    getLinkTarget() {
        let linkTarget = "";
        let linkTargetDefault = "_self";
        if(config.externalLinks != undefined) {
            linkTarget = "_blank"
            return linkTarget;
        } else {
            return linkTargetDefault;
        }
    }
    
    // adjustment layout class for the search box depending if the age selector is available
    searchAdjustmentClass(ageSelector) {
        if(ageSelector) {
            return "pure-u-16-24";
        } else {
            return "pure-u-21-24 adjust-border-radius";
        }
    }
    
    // adjustment layout class for the search results
    resultsSizeClass(size) {
        let resultsSizeClass = "";
        switch(size) {
          case "large":
             resultsSizeClass = "pure-u-sm-24-24 pure-u-md-24-24 pure-u-lg-24-24 pure-u-xl-22-24 offset-xl-1-24";
            break;
          case "medium":
            resultsSizeClass = "pure-u-sm-24-24 pure-u-md-22-24 offset-md-1-24 pure-u-lg-22-24 offset-lg-1-24 pure-u-xl-14-24 offset-xl-5-24";
            break;
          case "small":
            resultsSizeClass = "pure-u-sm-24-24 pure-u-md-22-24 offset-md-1-24 pure-u-lg-14-24 offset-lg-5-24 pure-u-xl-10-24 offset-xl-7-24";
            break;
          default:
            resultsSizeClass = "pure-u-sm-24-24 pure-u-md-24-24 pure-u-lg-24-24 pure-u-xl-22-24 offset-xl-1-24";
        }
        return resultsSizeClass;
    }

    toggleMenuDropdown(menuId) {
		let DISMISS_EVENT = (window.hasOwnProperty && window.hasOwnProperty('ontouchstart')) ? 'touchstart' : 'mousedown';
		let menuEl = document.getElementById(menuId);
		if(menuEl.classList.contains("hide-dropdown")){
			menuEl.classList.remove("hide-dropdown");
            menuEl.classList.add("show-dropdown");
            menuEl.style.display="block"
            if(menuId === "age-dropdown-menu"){
                let searchGroupWidth = document.getElementById("search-group").offsetWidth;
                let dropdownWidth = null;
                dropdownWidth = parseInt(searchGroupWidth)-30;
                menuEl.style.width = dropdownWidth+"px";
            }
			document.addEventListener(DISMISS_EVENT, function(e){
                let ageMenuSelector = document.querySelector('.age-selector-menu');
                let wfcHelpMenu = document.querySelector('#wfc-help-menu');
                let target = e.target
                let ageMenuSelectorFound = ageMenuSelector != null ? ageMenuSelector.contains(target) : false
                // console.log("target", target)
                if(!ageMenuSelectorFound && !wfcHelpMenu.contains(target)){
                    if( menuEl.classList.contains("show-dropdown")){
                        menuEl.classList.remove("show-dropdown");
                        menuEl.classList.add("hide-dropdown");
                        menuEl.style.display="none"
                    }
                }
			});
		}else{
			menuEl.classList.remove("show-dropdown");
            menuEl.classList.add("hide-dropdown");
            menuEl.style.display="none"
		}
	}

	openSelectedTab(e, tabName){
        var i, tabPanes, tablinks;
        tabPanes = document.getElementsByClassName("wfc-tab-pane");
        for (i = 0; i < tabPanes.length; i++) {
           tabPanes[i].className = tabPanes[i].className.replace(" wfc-tab-pane-active", " wfc-tab-pane-inactive");
        }
        tablinks = document.getElementsByClassName("pure-menu-item");
        for (i = 0; i < tablinks.length; i++) {
            tablinks[i].className = tablinks[i].className.replace(" pure-menu-selected", "");
        }
        document.getElementById(tabName).className+= " wfc-tab-pane-active";
        e.currentTarget.className += " pure-menu-selected";
	}


}

let styleStore = new StyleStore();
export default styleStore;
