import config from '../config';
import api from '../utils/api';
import audiencesStore from './AudiencesStore';
import userStore from '../stores/UserStore';
import cookies from 'cookies-js';

const THIRTY_DAYS = 30 * 24 * 60 * 60;

class SearchParamStore {

    constructor() {
        this.cseId = null;
        this.level = null;
        this.levels = [];
        this.query = null;
        this.scope = null;
        this.page = null;
        this.filter = "strict"; //default reading level filter state
        this.grouping = true;

        this.bindEvents();
    }

    getCseId() {
        return this.cseId;
    }

    getLevel() {
        return this.level;
    }
    
    getLevels() {
        return this.levels;
    }
    
    getFilter() {
        return this.filter;
    }

    getQuery() {
        return this.query;
    }

    getScope() {
        return this.scope;
    }

    getPage() {
        return this.page;
    }
    
    getGroupSources() {
        return this.grouping;
    }

    isSourceQuery() {
        return this.query && this.query.indexOf("site:") != -1;
    }
    
    getSourceName() {
        if(this.query && this.query.indexOf("site:") != -1) {
            let sourceUrl = this.query.split(":");
            return sourceUrl[1];
        }
    }

    bindEvents() {
        /*riot.control.on(riot.EVT.changeLevel, level => {
            this.level = level;
        });*/
        
        riot.control.on(riot.EVT.changeLevels, levels => {
            this.levels = levels;
            console.info("Reading levels set to " + levels);
        });
         
        riot.control.on(riot.EVT.changeQuery, query => {
            this.query = query;
            riot.control.trigger(riot.EVT.changeQuerySuccess, query);
        });

        riot.control.on(riot.EVT.changeScope, scope => {
            this.scope = scope;
        });

        riot.control.on(riot.EVT.changePage, page => {
            this.page = parseInt(page, 10);
        });

        riot.control.on(riot.EVT.changeCse, cseId => {
            if(config.saveUserStateInCookie) {
                cookies.set("wfc_cse", cseId, {expires: THIRTY_DAYS});
            }   
            this.cseId = cseId;
        });
        
        riot.control.on(riot.EVT.changeFilter, filter => {
            this.filter = filter;
        });
        
         riot.control.on(riot.EVT.changeGrouping, grouping => {
            this.grouping = grouping;
        });
    }

    updateLocationBar()  {
        let isFirst = true;
        let url = "/age-" + userStore.getAge() + "/search";
        let append = (key, val) => {
            if(val == null) return;
            if(isFirst) {
                url += "?";
                isFirst = false;
            } else {
                url += "&";
            }
            url += key + "=" + encodeURIComponent(val);
        }
        append("q", this.query);
        append("level", this.levels);
        append("filter", this.filter);
        append("page", this.page);
        append("scope", this.scope);
        append("cse", this.cseId);
        append("groupSources", this.grouping);

        if(riot.wnrouter.getPath() != url) {
            riot.wnrouter.setLocationBarWithoutRouting(url);
        }
    }
}

let searchParamStore = new SearchParamStore();

export default searchParamStore;
