import _ from 'lodash';
import counterpart from 'counterpart';
import config from '../config';

// Configure counterpart and export it as the default, so we can do import t from 'translate.js' and use t('key') for translation

const localeDefaults = {
    counterpart: {
        pluralize: (entry, count)=> entry[
            (count === 0 && 'zero' in entry)
                ? 'zero' : (count === 1) ? 'one' : 'other'
            ],
        formats: {
            date: {
                'default':  '%a, %e. %b %Y',
                'long':     '%A, %e. %B %Y',
                'short':    '%d.%m.%y'
            },
            time: {
                'default':  '%H:%M',
                'long':     '%H:%M:%S %z',
                'short':    '%H:%M'
            },
            datetime: {
                'default':  '%a, %e. %b %Y, %H:%M',
                'long':     '%A, %e. %B %Y, %H:%M:%S %z',
                'short':    '%d.%m.%y %H:%M'
            }
        }
    }
};

const nlJSON = _.assign(
    require('../locales/phrase.nl.json'), localeDefaults
);

const enJSON = require('../locales/phrase.en.json');

counterpart.registerInterpolations({
    public_path: __ENV__.PUBLIC_PATH
});
counterpart.registerTranslations('nl', nlJSON);
counterpart.registerTranslations('en', enJSON);
counterpart.setLocale(config.getDefaultLocale());

counterpart.wizenozeSetLocale = locale => {
    counterpart.setLocale(locale);
}

// make sure counterpart is available globally
window.t = counterpart;

export default counterpart;
