import config from '../config';
import api from '../utils/api';
import searchParamStore from './SearchParamStore';
import userStore from './UserStore';

class AutocompleteStore {
    constructor() {
        this.completions = [];
        this.query = null;
        this.inflightCounter = 0;

        this.bindEvents();
    }

    getQuery() {
        return this.query;
    }

    getApiQueryResult() {
        return this.apiQueryResult;
    }

    getCompletions() {
        return this.completions;
    }

    clear() {
        this.completions = [];
        this.query = null;    
    }

    bindEvents() {
        riot.control.on(riot.EVT.autocomplete, query => {
            this.inflightCounter++;
            let queryWords = query.split(" ", 1);
            
            if(this.completions.length > 0) {
                // we have existing completions, filter them using query as a prefix, then full update when network returns
                this.completions = _.filter(this.completions, x => x.includes(query.toLowerCase()));
                riot.control.trigger(riot.EVT.autocompleteSuccess, this);
            }
            
            this.autocomplete(query, searchParamStore, this.inflightCounter,
            result => {
                if(queryWords[0].length <= 10) { // request new sugestions only if the first word of query is shorter or equal than 10 characters
                    this.completions = result.results[0].completions;
                    result.query.readabilityFilter = searchParamStore.filter;
                    this.query = result.query.originalTerms;
                    this.apiQueryResult = result.query;
                    // have to filter audience: "none" and change it to {} so logging doesn't break                
                    if(this.apiQueryResult.audience == "none") this.apiQueryResult.audience = {};
                    riot.control.trigger(riot.EVT.autocompleteSuccess, this);
                }
            },
            error => {
                riot.control.trigger(riot.EVT.autocompleteFailed, error);
            })
        });
    }

    // REMOTE API CALLS

    async autocomplete(query, searchParamStore, inflightId, callback, errorCallback) {
        try {
            let params = {
                q: query,
                age: userStore.getAge(),
                readabilityLevel: searchParamStore.getLevel(),
                customSearchEngineId: searchParamStore.getCseId(),
                userUUID: userStore.getUUID(),
                sessionUUID: userStore.getSessionUUID()
            };

            params = _.defaults({}, params || {}, {
                age: 7,
                readabilityLevel: 3
            });
            
            let {data} = await api.doGet(config.autocompleteApiEndpoint, null, params);    
            if(inflightId == this.inflightCounter) {
                callback(data);
            }
        } catch(err) {
            if(inflightId == this.inflightCounter) {
                errorCallback(err);
            }
        }
    }
}

let autocompleteStore = new AutocompleteStore();

export default autocompleteStore;
