// Set the public path to either the CDN sdk.webforclassrooms.com or / depending on whether it's a dev with wfc_sdk.zip or the cdn loading wfc_sdk.js.
// TODO: see if we can detect when we're loaded from the CDN and set this correctly instead of needing two builds
__webpack_public_path__ = __ENV__.PUBLIC_PATH;

// Libs
import 'lodash';
import cookies from 'cookies-js';
import config from './config';

// Less CSS
require('./css/main.less');

// App
import './utils/translate'; // exposes window.t = counterpart
import './event_helper';
import './router';

// Stores
import searchParamStore from './stores/SearchParamStore';
import './stores/SearchResultsStore';
import './stores/AudiencesStore';
import './stores/AutocompleteStore';
import userStore from './stores/UserStore';
import styleStore from './stores/StyleStore';

export function init(params) {
  //TODO: validate parameters
  // set age on userstore
  console.info('Initialising WfC SDK with params', params);
  config.searchApiEndpoint = params['searchApiEndpoint'];
  config.autocompleteApiEndpoint = params['autocompleteApiEndpoint'];
  config.customSearchEngines = params['customSearchEngines'];
  config.saveUserStateInCookie = params['saveUserStateInCookie'] || true;
  config.scopes = params['scopes'] ||
    __ENV__.SCOPES || ['pages', 'videos', 'images'];
  console.info('Save user state? ' + config.saveUserStateInCookie);
  config.customStyles = params['customStyles'];
  config.externalLinks = params['externalLinks'];
  config.customHeaders = params['customHeaders'];
  config.customQuickGuide = params['customQuickGuide'] || false;
  config.showDefaultFooterLinks = params['showDefaultFooterLinks'];
  config.customFooterLinks = params['customFooterLinks'];
  config.showDefaultLegalLinks = params['showDefaultLegalLinks'];
  config.customLegalLinks = params['customLegalLinks'];
  config.showAgeSelector = params['showAgeSelector'];
  config.customAgeRange = params['customAgeRange'];
  config.showLevelSelector = params['showLevelSelector'];
  config.showCseSelector = params['showCseSelector'];
  config.showHeader = params['showHeader'];
  config.showFooter = params['showFooter'];
  config.showDefaultQuickGuide = params['showDefaultQuickGuide'];
  config.showHelpMenu = params['showHelpMenu'];
  config.customHtmlContent = params['customHtmlContent'];
  config.customCssFile = params['customCssFile'];
  config.resultsListWidth = params['resultsListWidth'];
  if (params['customLogo']) config.customLogo = params['customLogo'];
  if (params['customFooterLogo'])
    config.customFooterLogo = params['customFooterLogo'];
  if (params['customImageBanner'])
    config.customImageBanner = params['customImageBanner'];

  // set age from cookie, or from params, or stick with default
  let age = null;
  if (config.saveUserStateInCookie && cookies.get('wfc_age')) {
    age = parseInt(cookies.get('wfc_age'));
    if (isNaN(age) || age < 7 || age > 16) {
      age = null;
    } else {
      console.info('Setting age ' + age + ' from cookie');
    }
  }
  if (!age && 'age' in params && params['age']) {
    console.info('Setting age from init params');
    age = params['age'];
  }
  if (!age) {
    console.info('Setting default age');
    age = 12;
  }
  userStore.setAge(age);

  if (
    'language' in params &&
    params['language'] &&
    ['nl', 'en'].includes(params['language'])
  ) {
    console.info('Setting language', params['language']);
    t.wizenozeSetLocale(params['language']);
  }
  console.info(
    _.find(config.customSearchEngines, { default: true }) +
      ' ' +
      _.first(config.customSearchEngines)
  );
  searchParamStore.cseId = (_.find(config.customSearchEngines, {
    default: true,
  }) || _.first(config.customSearchEngines))['id'];
  if (
    config.saveUserStateInCookie &&
    cookies.get('wfc_cse') &&
    _.find(config.customSearchEngines, { id: parseInt(cookies.get('wfc_cse')) })
  ) {
    searchParamStore.cseId = parseInt(cookies.get('wfc_cse'));
    console.info('Set CSE from cookie: ' + searchParamStore.cseId);
  }

  config.loadCustomCssFile();

  riot.wnrouter.init(params['anchorId']);
  return true;
}
