class AudiencesStore {
    constructor() {
        this.DEFAULT_AGE = 12;
        this.MAX_AGE = 18;
        this.MIN_AGE = 7;
        
        this.ages = {
            0:  { defaultReadingLevel: 1 },
            7:  { defaultReadingLevel: 1 },
            8:  { defaultReadingLevel: 1 },
            9:  { defaultReadingLevel: 2 },
            10: { defaultReadingLevel: 2 },
            11: { defaultReadingLevel: 3 },
            12: { defaultReadingLevel: 3 },
            13: { defaultReadingLevel: 4 },
            14: { defaultReadingLevel: 4 },
            15: { defaultReadingLevel: 4 },
            16: { defaultReadingLevel: 4 },
            17: { defaultReadingLevel: 4 },
            18: { defaultReadingLevel: 4 }
        };

        this.readingLevels = {
            1: { audienceKey: 'groep34'},
            2: { audienceKey: 'groep56'},
            3: { audienceKey: 'groep78'},
            4: { audienceKey: 'vo2'},
            5: { audienceKey: 'vo3'}
        };

        // For maintaining backwards compatibility with old bookmarks. @see BookmarkCompatibilityCtrl
        this.oldGroepsMappedToAges = {
            '3&4': 8,
            '5&6': 10,
            '7&8': 12,
            'voI': 12,
            'voII': 14,
            'voIII': 18
        };

        this.helpPageMapping = {
            1: 'vragen-geel',
            2: 'vragen-oranje',
            3: 'vragen-groen',
            4: 'vragen-blauw',
            5: 'vragen-zwart'
        };
    }

    getAges() { 
        return Object.keys(this.ages).map(a => parseInt(a, 10)); 
    }

    getReadingLevels() {
        return Object.keys(this.readingLevels).map(a => parseInt(a, 10)); 
    }

    getDefaultLevel(age) {
        if(!this.ages[age]) return 3;
        return this.ages[age].defaultReadingLevel; 
    }

    getLayoutForAge(age) { 
        if(age < 8) return "grid";
        return "list";
    }

    getDefaultAge() {
        return this.DEFAULT_AGE; 
    }

    getOldGroepsMappedToAges() {
        return this.oldGroepsMappedToAges; 
    }

    getHelpPageMapping(level) {
        return this.helpPageMapping[level]; 
    }
}

let audiencesStore = new AudiencesStore();
export default audiencesStore;
