import config from '../config';
import api from '../utils/api';
import searchParamStore from './SearchParamStore';
import userStore from './UserStore';
import audiencesStore from './AudiencesStore';

class SearchResultsStore {

    constructor() {
        this.clear();

        this.bindEvents();

        this.inflight = 0;
    }

    hasQueryExecuted() {
        return this.query != null;
    }

    clear() {
        this.resultSetId = null;
        this.didYouMean = null;
        this.pagination = null;
        this.results = null;
        this.query = null;
    }

    getResultSetId() {
        return this.resultSetId;
    }

    getDidYouMean() {
        return this.didYouMean;
    }

    getPagination() {
        return this.pagination;
    }

    getResults() {
        return this.results;
    }
    
    getDisplayUrl() {
        let resultsObj = this.results;
        let displayUrl = "";
        for(let key in resultsObj){
            displayUrl = resultsObj[key].displayUrl;
            return displayUrl;
            break;
        }
    }

    getQuery() {
        return this.query;
    }

    getEffectiveLevel(result) {
        return _(result.audiences).map('level').filter().head();
    }

    getInflight() {
        return this.inflight;
    }

    hasSuitableResults() {
        const level =
            this.query &&
            this.query.readingLevel &&
            _.first(this.query.readingLevel);

        if (!level) {
            //return false;
        }

        const documentLevels = _(this.results)
            .map(this.getEffectiveLevel)
            .filter();

        // result pages with non-classified results such as video result pages
        // are always suitable, hence the isEmpty()
        return documentLevels.isEmpty() || documentLevels.includes(level);
    }

    bindEvents() {
        riot.control.on(riot.EVT.changeResults, () => {
            this.inflight++;
            this.search(searchParamStore, 
            result => {
                this.didYouMean = result.didYouMean;
                this.pagination = result.pagination;
                this.results = this.preProcessResults(result.results);
                this.query = result.query;
                this.resultSetId = result.resultSetId;
                searchParamStore.updateLocationBar();
                this.inflight--;
                riot.control.trigger(riot.EVT.changeResultsSuccess, this);

            },
            error => {
                console.error("Got error", error);
                this.inflight--;
                riot.control.trigger(riot.EVT.changeResultsFailed, error);
            })
        });
    }

    // replace curly braces with html entities so riot doesn't try to evaluate them when passsed as option to unescaped_html tag
    sanitizeForRiot(html) {
        if(html) return html.replace(/\}/g, "&rbrace;").replace(/\{/g, "&lbrace;");
        return html;
    }

    preProcessResults(results) {
        // normal results, return them
        if(results && results.length > 0 && !results[0].subResults) {
            for(let result of results) {
                result.summaryText = this.sanitizeForRiot(result.summaryText);
            }
            return results;
        }
        // results grouped by source, unpack
        let newResults = [];
        for(let resultGroup of results) {
            if(!resultGroup.subResults) {
                continue;
            }
            let result = resultGroup.subResults[0];
            if(resultGroup.subResults.length > 1) {
                result.subResults = resultGroup.subResults.slice(1, 4);
                for(let subResult of result.subResults) {
                    subResult.summaryText = this.sanitizeForRiot(subResult.summaryText);
                }
            }
            result.summaryText = this.sanitizeForRiot(result.summaryText);
            result.groupKey = resultGroup.key;
            result.groupShowAllQueryString = resultGroup.showAllQueryString;
            result.groupTotalResults = resultGroup.totalResults;
            newResults.push(result);
        }
        return newResults;
    }
    
    mustGroupSources(level, groupSources) {
        if(searchParamStore.isSourceQuery()) {
            return false;
        } else if(searchParamStore.getScope() && searchParamStore.getScope() == 'pages') {
            return groupSources;
        } else if(searchParamStore.getScope() && searchParamStore.getScope() != 'pages') {
            return false;
        } else {
            return groupSources;
        }
    }

    // REMOTE API CALLS

    async search(searchParamStore, callback, errorCallback) {
        try {
            let params = {
                q: searchParamStore.getQuery(),
                startPage: searchParamStore.getPage(),
                age: userStore.getAge(),
                readabilityLevel: _.join(searchParamStore.getLevels(), ','),
                readabilityFilter: searchParamStore.getFilter(),
                resultType: searchParamStore.getScope(),
                groupSources: searchParamStore.getGroupSources(),
                customSearchEngineId: searchParamStore.getCseId(),
                userUUID: userStore.getUUID(),
                sessionUUID: userStore.getSessionUUID()
            };

            params = _.defaults({}, params || {}, {
                pageSize: searchParamStore.getScope() == 'images' ? 10 : 8,
                startPage: 1,
                age: 7,  // default to strict filter
                readabilityLevel: 3,
                readabilityFilter: 'moderate',
                resultType: 'pages',
                groupSources: true
            });
            
            params.groupSources = this.mustGroupSources(params.readabilityLevel,params.groupSources);
            
            let {data} = await api.doGet(config.searchApiEndpoint, null, params);
            callback(data);
        } catch(err) {
            errorCallback(err);
        }
    }
}

let searchResultsStore = new SearchResultsStore();

export default searchResultsStore;
